import gql from "graphql-tag";
export default {
  apollo: {
  movie_by_pk: {
      query: gql`
        query MyQuery($id: Int!) {
          movie_by_pk(id: $id) {
            Author
            Description
            Name
            Rating
            Release_Date
            genre_id
            poster_path
            video_path
movie_genre_fk {
      genre_name
    }
          }
        }
      `,
      variables() {
          return {
              
              id: this.$route.params.id,
          }
      },
    },

    genre: gql`
    query MyQuery {
  genre {
    id
    genre_name
  }
}
`,


  },
  
  data(){
    return {
      id: this.$route.params.id,
      isStreaming: false
    }
  },

//   async created() {
// this.id = this.$route.params.id
//   },
  methods: {
    redPage(){
      this.$router.push('/')
    }
  }
};
